  font-family: 'Army Rust';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  min-height: 100vh;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-repeat: no-repeat;
  background-size: cover;
}

button {
  display: inline-block;
  outline: 0;
  border: 0;
  cursor: pointer;
  background-color: white;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 16px;
  font-weight: 600;
  color: #2d3748;
  border: 1px solid #cbd5e0;
  line-height: 26px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  font-family: 'Army Rust';
}

button:hover {
  transform: scale(1.2);
  transition: transform 0.3s ease;
}

button:disabled {
  color: darkgray;
  background-color: darkgray;
}

.main-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
}

.header-container {
  margin-top: 5px;
  width: 50%;
  height: 15%;
}

.header-text {
  text-align: center;
  font-size: 6rem;
  font-family: 'Army Rust';
  color: rgb(182, 169, 169);
  font-weight: 100;
}

.content-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 99%;
  margin-top: 25px;
  justify-content: center;
}

.instructions-container {
  width: 15%;
  height: 85%;
}

.instructions-container p {
  font-family: 'Army Rust';
  font-size: 2.2rem;
  color: rgb(182, 169, 169);
}

.instruc-text {
  font-size: 69px;
  font-family: 'Army Rust';
}

.ship-container {
  padding: 10px;
  width: 300px;
  height: 400px;
  box-shadow: 0 0 0.5rem 0 rgb(202, 214, 224);
  border-radius: 45px;
  margin-top: 45px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.ship-container button {
  width: 40%;
  align-self: center;
  font-size: 1.2rem;
}

.direction-container {
  display: flex;
  justify-content: space-around;
  width: 99%;
  height: 10%;
}

.player-board-container,
.computer-board-container {
  margin-top: 30px;
  border: solid 1px rgb(172, 167, 167);
  border-radius: 5px;
  width: 500px;
  height: 500px;
  display: grid;
  grid-template-rows: repeat(10, 1fr);
  grid-template-columns: repeat(10, 1fr);
}

.row {
  border: solid 1px rgb(172, 167, 167);
  border-radius: 5px;
  width: 100%;
  height: 100%;
}

.hover {
  background-color: green;
}

.clicked {
  background-color: blue;
}

.error {
  background-color: red;
}

.highlight {
  background-color: yellow;
}

.target {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-repeat: no-repeat;
  background-size: cover;
}

.random-highlight {
  background-color: green;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.board-ship-container {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 60px;
}

.start-container {
  margin-top: 15px;
  width: 40%;
  height: 10%;
  align-self: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.start-container button {
  width: 6rem;
  /* height: 2.2rem; */
}

.start-btn {
  background-color: green;
}

.reset-btn {
  background-color: red;
}

.random-btn {
  background-color: yellow;
}

.start-btn,
.reset-btn,
.random-btn {
  font-size: 1.4rem;
  color: black;
}

.missed {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #ccc;
}

.hit {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
  background-repeat: no-repeat;
  background-size: cover;
}

#game-over {
  width: 25%;
  height: 45%;
  border-radius: 25px;
  box-sizing: border-box;
  overflow: hidden;
}

.dialog-div {
  width: 100%;
  height: 100%;
  font-family: 'Army Rust';
}

.dialog-header-container {
  width: 100%;
  height: 20%;
  box-sizing: border-box;
}

.dialog-header-text,
.para-text {
  text-align: center;
  font-family: 'Army Rust';
  font-size: 3rem;
}

.dialog-para-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  height: 100%;

  box-sizing: border-box;
  overflow: hidden;
  gap: 15px;
}

.para-text-container {
  width: 100%;
  height: 50%;
}

.new-game {
  display: flex;
  margin-left: 20px;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 15%;
}

.new-game button {
  background-color: green;
  color: black;
  font-size: 1.4rem;
  width: 8rem;
}
